@media only screen and (max-width: 1024px) {
	.product__top{
		height: 285px;
		.mount{
			bottom: 0;
			position: absolute;
			top: initial;
			background-position: 50% 100%;
		}
	} 
	.top_text-first{
		font-size: 40px;
		margin-top: 20px;
	}
	.top_text-second{
		font-size: 25px;
	}
	.top_text-third{
		font-size: 15px;
	}
	.product-text{
		padding-bottom: 0;
	}
	.product__bottom{
		.product__items{
			margin: 0;
			.items_text{
				top: 15px;
				
				p{
					width: 90%;
				}
				h3{
					width: 100%;
				}
				
			}
		}
	}
	.product__bottom{
		height: auto;
	}
	.product__form_left{
		h4{
			margin: 10px 0;
		}
	}
	.product__form_right-form{
		.input-content{
			width: 55%;
		}
		.form-button{
			margin-top: 0;
			margin-left: 15px;
			height: 40px;
		}
	}
}

