@media only screen and (max-width: 736px) {
	.product__top{
		height: 230px;
		padding-top: 0;
		.mount{
			bottom: 0;
			position: absolute;
			top: initial;
			background-position: 50% 100%;
		}
	}
	.top_text-first{
		font-size: 30px;
		margin-top: 0px;
	}
	.top_text-second{
		font-size: 18px;
		margin-bottom: 10px;
	}
	.top_text-third{
		font-size: 15px;
		margin-top: 10px;
	}
	.product-text{
		ul{
			li{
				width: 100%;
			}
		}
	}
	.product__bottom{
		height: auto;
		.product__items{
			margin: 15px 0;
			.items_thumb{
				width: 100%;
			}
			.items_text{
				width: 100%;
				p{
					width: 100%;
				}
			}
		}
	}
	.product__form{
		height: auto;
		.product__form_left{
			width: 100%;
			
		}
		.product__form_right{
			width: 100%;
			height: auto;
			.input-content{
				width: 97%;
				float: left;
			}
			.form-button{
				width: 97%;
				height: 35px;
				margin: 10px 0 0;
			}
		}
	}
}

